.addnew-container {
  position: relative;
}

.addnew-image {
  display: -ms-flexbox;
  display: flex;
}

.addnew-image-inner {
  position: relative;
  width: 33%;
  cursor: -webkit-grab;
}

.addnew-image img {
  max-width: 100%;
}

.addnew-image-handle {
  position: absolute;
  right: 0;
  top: 0;
}

.addnew-image-handle-text {
  position: absolute;
  -webkit-transform: translate(-100%, 6px);
      -ms-transform: translate(-100%, 6px);
          transform: translate(-100%, 6px);
  top: 0;
  font-size: 10px;
}

.addnew-image-edit {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.addnew-button {
  width: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
}



