html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,button,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}
html,body{-webkit-tap-highlight-color:transparent}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}
body{line-height:1}
ol,ul{list-style:none}
blockquote,q{quotes:none}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}
table{border-collapse:collapse;border-spacing:0}
*,:before,:after{-webkit-box-sizing:border-box;box-sizing:border-box}
body{color:#4B4B4B}
button{color:inherit;background:none;outline:none}
button,button:focus,button::-moz-focus-inner{border:none;outline:none}
input{border:none;outline:none}
input,input:focus,input::-moz-focus-inner{border:none;outline:none}
strong,b{font-weight:700}
:focus{outline:none}
* {-webkit-font-smoothing: antialiased}
html { -webkit-user-select: none}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}



